$color_1: #FFF;
$color_2: #fff;
$color_3: #222;
$color_4: #1EACC7;
$color_5: #202020;
$font-family_1: 'Roboto';
$font-family_2: Roboto;

* {
	font-family: $font-family_1;
}
header {
	position: relative;
}
a {
	text-decoration: none;
}
nav {
	height: 50px;
	background-color: #212121;
}
.menu {
	display: flex;
	justify-content: space-between;
	padding: 13px 70px;
	margin: 0;
	list-style-type: none;
	.menu_link {
		color: $color_1;
		font-size: 15px;
		font-weight: 300;
	}
	.menu_item {
		border-right: 2px dotted black;
		padding-right: 28px;
	}
}
.subheader {
	position: absolute;
	width: 100%;
	height: 87px;
	background-color: rgba(0, 0, 0, .6);
	.subheader_logo {
		display: block;
		max-width: 100%;
		margin-top: 16px;
	}
	.subheader_official {
		margin-top: 6px;
		opacity: 0.5;
		color: $color_1;
		font-size: 12px;
		font-weight: 400;
	}
	.subheader_call {
		color: $color_1;
		text-align: right;
		margin-top: 19px;
		font-size: 12px;
		font-weight: 300;
	}
	.subheader_phone {
		display: block;
		color: $color_1;
		text-align: right;
		font-size: 22px;
		font-weight: 400;
		margin-top: 8px;
		line-height: 12px;
	}
	.subheader_btn {
		margin-top: 19px;
		width: 172px;
		height: 46px;
		border-radius: 4px;
		background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
		box-shadow: 0px -3px 0px 0px rgba(0, 0, 0, 0.12) inset;
		border: none;
		color: $color_1;
		text-shadow: 1px 1px 0px 0px rgba(0, 0, 0, 0.21);
		font-size: 14px;
		font-weight: 400;
		text-transform: uppercase;
	}
}
.promo {
	min-height: 800px;
	background: url('../img/uber_bg.jpg') center center/cover no-repeat;
	padding: 150px 0 135px 0;
	.promo_header {
		color: $color_1;
		text-align: center;
		font-size: 60px;
		font-weight: 700;
		line-height: 60px;
		margin: 0;
	}
	.promo_subheader {
		color: $color_1;
		text-align: center;
		font-size: 37px;
		font-weight: 900;
		line-height: 36px;
		margin: 0;
		margin-top: 22px;
	}
	.promo_descr {
		margin-top: 16px;
		color: $color_1;
		text-align: center;
		font-size: 18px;
		font-weight: 400;
		line-height: 24px;
	}
	.promo_btm {
		display: block;
		margin: 0 auto;
		margin-top: 116px;
		width: 259px;
		height: 63px;
		border-radius: 4px;
		background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
		box-shadow: 0px -4px 0px 0px rgba(0, 0, 0, 0.12) inset;
		border: none;
		color: $color_1;
		text-align: center;
		text-shadow: 1px 1px 0px 0px rgba(0, 0, 0, 0.21);
		font-family: $font-family_2;
		font-size: 18px;
		font-weight: 400;
		text-transform: uppercase;
	}
}
.label {
  margin: 0 auto;
  width: 115px;
  height: 27px;
  padding: 6px 12px;
  background-color: #000;
  border: none;
  color: $color_2;
  text-align: center;
  font-size: 11px;
  font-weight: 300;
  text-transform: uppercase;
  text-align: center;
}
.title {
  color: $color_3;
  margin-top: 33px;
  text-align: center;
  font-size: 38px;
  font-weight: 700;
  text-transform: uppercase;
}
.subtitile{
  margin-top: 19px;
  color: #222;
  text-align: center;
  font-size: 17px;
  font-weight: 400;
}
.reason {
	min-height: 849px;
	padding: 18px 0 64px 0;
	.reason_block {
		display: flex;
		margin-top: 35px;
		align-items: center;
		justify-content: center;
		.reason_round {
			display: flex;
			justify-content: center;
			align-items: center;
			background-color: #1EACC7;
			width: 116px;
			height: 116px;
			border-radius: 100%;
			margin-right: 26px;
		}
		.reason_descr {
			width: 371px;
			.reason_subtitile {
				color: $color_4;
				font-size: 17px;
				font-weight: 400;
				line-height: 21px;
			}
			.reason_text {
				color: $color_5;
				font-size: 15px;
				font-weight: 400;
				line-height: 21px;
			}
		}
	}
}
.mobile{
  min-height: 864px;
}